<script>
import * as echarts from 'echarts'
import getTime from '../utils/time.js'
import VisualMapStorage from '@/components/visual/VisualMapStorage.vue'
export default {
  name: '',
  components: {
    VisualMapStorage,
  },
  data() {
    return {
      time: 'yyyy-MM-dd HH:mm:ss',
      gridTitle: '预警',
      marker: null,
      map: null,
    }
  },
  methods: {
    // 天地图部分
    renderMap() {
      // 设置地图级别
      var zoom = 12
      //初始化地图对象
      this.map = new T.Map('mapDiv')
      //设置显示地图的中心点和级别
      this.map.centerAndZoom(new T.LngLat(117.77, 39.05), zoom)
      //创建缩放平移控件对象
      var control = new T.Control.Zoom()
      //添加缩放平移控件
      this.map.addControl(control)
      // 设置地图显示范围
      // this.map.setMaxBounds(
      //   new T.LngLatBounds(
      //     new T.LngLat(117.65, 38.92),
      //     new T.LngLat(117.89, 39.09)
      //   )
      // )
      //允许鼠标滚轮缩放地图
      this.map.enableScrollWheelZoom()
      // 允许双击放大地图
      this.map.enableDoubleClickZoom()
      // 允许通过小键盘 + - 按钮缩放地图
      this.map.enableKeyboard()

      var imageURL =
        'http://t0.tianditu.gov.cn/img_w/wmts?' +
        'SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=img&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles' +
        '&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&tk=5b02fc2503dcd0c9d3abf727cd984fd2'
      //创建自定义图层对象
      var lay = new T.TileLayer(imageURL, {
        minZoom: 1,
        maxZoom: 18,
        opacity: 0.8,
      })
      //将图层增加到地图上
      this.map.addLayer(lay)

      // 创建标记点
      this.createMarkers()
    },

    createMarkers() {
      var markers = [
        { lng: 117.703925, lat: 39.049074 },
        { lng: 117.750925, lat: 39.032074 },
        { lng: 117.763925, lat: 38.996074 },
        { lng: 117.693925, lat: 39.009074 },
        { lng: 117.783925, lat: 39.039074 },
      ]

      markers.forEach((v, i) => {
        //创建图片对象
        var icon = new T.Icon({
          iconUrl:
            i % 2 == 0
              ? require('../assets/images/pl.png')
              : require('../assets/images/pl2.png'),
          iconSize: new T.Point(30, 26),
          iconAnchor: new T.Point(10, 20),
        })
        //向地图上添加自定义标注
        var marker = new T.Marker(new T.LngLat(v.lng, v.lat), {
          icon: icon,
        })

        // var infoWin1 = new T.InfoWindow()
        var sContent = `<div style="width:80px;height:49px;margin-bottom:50px">
          <p style="color:white">${i % 2 == 0 ? '预警' : '事件'}</p>
          <p style="font-weight:bold;">1280</p>
        </div>`
        this.createInfo(v.lng, v.lat, sContent)

        this.map.addOverLay(marker)
        marker.addEventListener('click', this.MarkerClick)
      })
    },

    MarkerClick(e) {
      // alert(e.lnglat.getLng() + ',' + e.lnglat.getLat())
      this.gridTitle = ['事件', '预警'][parseInt(Math.random() * 2)]
    },

    //创建信息窗口
    createInfo(lng, lat, content) {
      // 通过经纬度创建位置对象
      let lnglat = new T.LngLat(lng, lat)
      //创建信息窗口对象
      let infoWin = new T.InfoWindow()
      // 窗口对象中设置经纬度信息
      infoWin.setLngLat(lnglat)
      //设置信息窗口要显示的内容
      infoWin.setContent(content)
      //向地图上添加信息窗口
      this.map.addOverLay(infoWin)
    },

    // 右侧园区货物
    gardenGoodsInit() {
      let chart = echarts.init(document.getElementById('garden-goods'))

      let option = {
        legend: {
          top: '10%',
          right: 10,
          orient: 'vertical',
          textStyle: {
            color: '#fff',
          },
        },
        series: [
          {
            name: '面积模式',
            type: 'pie',
            radius: [20, 60],
            center: ['50%', '50%'],
            roseType: 'area',
            label: {
              formatter: '{c}%',
            },
            data: [
              { value: 24, name: 'A货物' },
              { value: 18, name: 'B货物' },
              { value: 18, name: 'C货物' },
              { value: 10, name: 'D货物' },
              { value: 20, name: 'E货物' },
              { value: 10, name: 'F货物' },
            ],
          },
        ],
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 高风险作业点
    riskOperateInit() {
      let chart = echarts.init(document.getElementById('risk-operate'))
      var data = [
        {
          name: '高风险地区 二',
          value: 20,
          itemStyle: {
            color: '#0092FF',
          },
          label: {
            color: '#0092FF',
          },
        },
        {
          name: '',
          value: 10,
          itemStyle: {
            color: '#01E3FC',
          },
          label: {
            formatter: ' ',
          },
          children: [
            {
              name: '高风险地区 一',
              value: 10,
              label: {
                color: 'rgb(0,166,152)',
              },
              itemStyle: {
                color: 'rgba(0,166,152,.8)',
              },
            },
          ],
        },
      ]
      let option = {
        series: {
          type: 'sunburst',
          emphasis: {
            focus: 'ancestor',
          },
          data: data,
          radius: ['20%', '90%'],
          center: ['50%', 100],
          label: {
            rotate: 0,
            align: 'left',
            distance: 36,
            formatter: '{b}\n{c|{c}} 个',
            lineHeight: 20,
            rich: {
              c: {
                fontSize: 20,
              },
            },
          },
          itemStyle: {
            borderColor: 'none',
          },
        },
      }
      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 右侧应急资源
    emerResInit() {
      let chart = echarts.init(document.getElementById('emer-res'))

      let option = {
        grid: {
          x: '10%',
          y: '22%',
          bottom: '15%',
          right: '2%',
        },
        legend: {
          left: 'right',
          textStyle: {
            color: '#fff',
          },
        },
        tooltip: {},
        dataset: {
          source: [
            ['product', '应急资源', '公共安全设施'],
            ['分类一', 2000, 1400],
            ['分类二', 3300, 2800],
            ['分类三', 1200, 600],
            ['分类四', 1200, 1400],
          ],
        },
        xAxis: {
          type: 'category',
          axisLabel: {
            color: '#fff',
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          name: '单位：个',
          interval: 800,
          max: 3200,
          axisLabel: {
            color: '#fff',
          },
        },
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [
          {
            type: 'bar',
            itemStyle: {
              color: 'rgba(145,204,117,.5)',
              borderWidth: 2,
              borderColor: 'rgb(145,204,117)',
            },
            barWidth: 20,
          },
          {
            type: 'bar',
            itemStyle: {
              color: 'rgba(218,142,44,.5)',
              borderWidth: 2,
              borderColor: 'rgb(218,142,44)',
            },
            barWidth: 20,
          },
        ],
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 底部园区危险源
    showDangerInit() {
      var colors = ['#4167BA', '#DA884B', '#FFC849', '#3597BD']
      let chart = echarts.init(document.getElementById('danger-sts'))
      let chartData = [
        {
          name: '一级危险源',
          value: 570,
          value2: 4,
          unit: '家',
        },
        {
          name: '二级危险源',
          value: 42,
          value2: 8,
          unit: '家',
        },
        {
          name: '三级危险源',
          value: 211,
          value2: 17,
          unit: '家',
        },
        {
          name: '四级危险源',
          value: 84,
          value2: 29,
          unit: '家',
        },
      ]
      let arrName = []
      let arrValue = []
      let sum = 0,
        sum2 = 0
      let pieSeries = [],
        lineYAxis = []

      // 数据处理
      chartData.forEach((v, i) => {
        arrName.push(v.name)
        arrValue.push(v.value)
        sum = sum + v.value
        sum2 += v.value2
      })

      // 图表option整理
      chartData.forEach((v, i) => {
        pieSeries.push({
          name: '重大危险源',
          type: 'pie',
          clockwise: false,
          emphasis: {
            //鼠标移入变大
            scale: false,
          },
          radius: [45 - i * 10, 50 - i * 10],
          center: ['30%', '55%'],
          label: {
            show: false,
          },
          data: [
            {
              value: sum - v.value,
              name: v.name,
              itemStyle: {
                color: 'transparent',
              },
            },
            {
              value: v.value,
              name: '',
              itemStyle: {
                color: colors[i],
                borderRadius: '50%',
              },
            },
          ],
        })
        v.percent = ((v.value2 / sum2) * 100).toFixed() + '%'
        lineYAxis.push({
          value: i,
        })
      })

      var option = {
        grid: {
          top: '10%',
          bottom: '4%',
          left: '60%',
          containLabel: false,
        },
        yAxis: [
          {
            type: 'category',
            inverse: true,
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              formatter: function (params) {
                let item = chartData[params]
                return `{circle|●} {name|${item.name}}{value|${item.value2}}{unit|家}\n{percent|${item.percent}}`
              },
              interval: 0,
              inside: true,
              fontSize: 14,
              rich: {
                name: {
                  color: '#568A9C',
                  fontSize: 12,
                },
                percent: {
                  color: '#fff',
                  fontSize: 12,
                  padding: [0, 0, 0, 10],
                },
                value: {
                  color: '#568A9C',
                  fontSize: 12,
                  padding: [0, 0, 0, 10],
                },
              },
            },
            data: lineYAxis,
          },
        ],
        xAxis: [
          {
            show: false,
          },
        ],
        series: pieSeries,
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 底部隐患情况 - 总隐患数量
    dangerAnalysisInit() {
      let chart = echarts.init(document.getElementById('danger-analysis'))

      let option = {
        color: ['#80FFA5', '#00DDFF'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#fff',
            },
            textStyle: {
              fontSize: 10,
            },
          },
        },
        legend: {
          left: 'right',
          itemWidth: 10,
          icon: 'diamond',
          textStyle: {
            color: '#fff',
            fontSize: 10,
          },
        },
        grid: {
          top: '30%',
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true,
        },
        xAxis: [
          {
            type: 'category',
            axisLabel: {
              color: '#fff',
            },
            boundaryGap: false,
            data: [
              '1月',
              '2月',
              '3月',
              '4月',
              '5月',
              '6月',
              '7月',
              '8月',
              '9月',
              '10月',
              '11月',
              '2月',
            ],
          },
        ],
        yAxis: [
          {
            type: 'value',
            name: '隐患趋势分析',
            axisLabel: {
              color: '#fff',
            },
            interval: 25,
            max: 100,
            splitLine: {
              lineStyle: {
                type: 'dotted',
              },
            },
          },
        ],
        series: [
          {
            name: '当期值',
            type: 'line',
            smooth: true,
            lineStyle: {
              width: 0,
            },
            showSymbol: false,
            areaStyle: {
              opacity: 0.8,
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: 'rgba(1,215,239)',
                },
                {
                  offset: 1,
                  color: 'rgba(27,126,217)',
                },
              ]),
            },
            emphasis: {
              focus: 'series',
            },
            data: [0, 55, 80, 50, 60, 70, 60, 70, 40, 70, 20, 0],
          },
          {
            name: '同期值',
            type: 'line',
            smooth: true,
            lineStyle: {
              width: 0,
            },
            showSymbol: false,
            areaStyle: {
              opacity: 0.8,
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: 'rgba(27,126,217)',
                },
                {
                  offset: 1,
                  color: 'rgba(1,126,130)',
                },
              ]),
            },
            emphasis: {
              focus: 'series',
            },
            data: [0, 35, 50, 70, 40, 90, 15, 35, 12, 50, 80, 10],
          },
        ],
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 底部园区企业建筑
    buildBarInit() {
      let chart = echarts.init(document.getElementById('build-bar'))

      var dataAxis = ['企业一', '企业二', '企业三', '企业四', '企业五']
      var data = [1500, 3000, 500, 4200, 3500]
      var yMax = 50
      var dataShadow = []

      for (var i = 0; i < data.length; i++) {
        dataShadow.push(yMax)
      }

      let option = {
        grid: {
          left: '15%',
          top: '15%',
          bottom: '15%',
          right: '5%',
        },
        xAxis: {
          data: dataAxis,
          axisLabel: {
            color: '#fff',
          },
          axisTick: {
            show: false,
          },
          axisLine: {
            show: false,
          },
          z: 10,
        },
        yAxis: {
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLabel: {
            color: '#336580',
          },
        },
        dataZoom: [
          {
            type: 'inside',
          },
        ],
        series: [
          {
            type: 'bar',
            barWidth: 20,
            itemStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: '#32647F' },
                { offset: 1, color: '#4E8987' },
              ]),
            },
            data: data,
          },
        ],
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },
  },
  mounted() {
    this.$nextTick(function () {
      this.renderMap()
      this.showDangerInit()
      this.gardenGoodsInit()
      this.dangerAnalysisInit()
      this.buildBarInit()
      this.emerResInit()
      this.riskOperateInit()
    })
  },
  created() {
    let timeObj = getTime.getTime()
    this.time = timeObj.dayTime()

    this.timer = setInterval(() => {
      this.time = timeObj.dayTime()
    }, 1000)
  },
  destroyed() {
    clearInterval(this.timer)
  },
}
</script>
